import { doNotRender } from "sam-pattern";
import API from "./api";

export default {
  actions: [
    (value) => {
      return {
        todo: value
      };
    }
  ],
  acceptors: [
    model => async (proposal) => {
      if (proposal.todo) {
        const newTodo = await API.createTodo({
          title: proposal.todo,
          completed: false
        });

        model.items.push(newTodo);
      }
    }
  ],
  naps: [
    doNotRender
  ]
}
