import API from "./api";

export default {
  actions: [
    () => {
      return API.getTodos().then(todos => {
        const transformedTodos = todos.reduce((hash, todo) => {
          hash[todo.id] = todo;

          return hash;
        }, {});

        return {
          apiItems: transformedTodos
        };
      });
    }
  ],

  acceptors: [
    (model) => (proposal) => {
      if (proposal.apiItems) {
        model.items = proposal.apiItems;
      }

      return model;
    }
  ]
}
